<script>
import Vue from "vue";
import navbar from "../components/Navbar.vue";
import show from "../components/Show.vue";
import footer from "../components/footer.vue";
import axios from "axios";
import VueAxios from "vue-axios";
import ElementUI from "element-ui";
import "element-ui/lib/theme-chalk/index.css";
Vue.use(VueAxios, axios, ElementUI);
export default {
  components: {
    navbar,
    footer,
    show,
  },
  data() {
    return {
      showPicture: "true",
      contact: {
        name: "",
        email: "",
        phone_number: "",
        message: "",
      },
      subscribe: {
        email: "",
      },
    };
  },

  methods: {
    contactUs() {
      axios
        .post(
          "https://hotel-project-cafb0-default-rtdb.firebaseio.com/contact.json",
          this.contact,
          {
            headers: {
              "Access-Control-Allow-Origin": "*",
              "Access-Control-Allow-Headers": "*",
            },
          }
        )
        .then((res) => {
          console.log(res);
          this.$message({
            message: "Message Sent!",
            type: " success",
          });
          this.contact = {};
        });
    },
    showMore() {
      this.showPicture = false;
    },
    showLess() {
      this.showPicture = true;
    },

    addSub() {
      this.subscribe.created_at = new Date();
      axios
        .post(
          "https://hotel-project-cafb0-default-rtdb.firebaseio.com/subscribe.json",
          this.subscribe,
          {
            headers: {
              "Access-Control-Allow-Origin": "*",
              "Access-Control-Allow-Headers": "*",
            },
          }
        )
        .then((res) => {
          console.log(res);
          this.$message({
            message: "Subsciption successful",
            type: " success",
          });
          this.subscribe = {};
        });
    },
  },
};
</script>
